@import url(https://fonts.googleapis.com/css?family=Do+Hyeon|Noto+Sans+KR|Teko&display=swap);
@import url(https://fonts.googleapis.com/css?family=Do+Hyeon|Jua|Open+Sans:400,600,700,800&display=swap);
@import url(https://fonts.googleapis.com/css?family=Do+Hyeon|Jua|Open+Sans:400,600,700,800&display=swap);
@import url(https://fonts.googleapis.com/css?family=Do+Hyeon|Noto+Sans+KR|Teko&display=swap);
@import url(https://fonts.googleapis.com/css?family=Do+Hyeon|Jua|Open+Sans:400,600,700,800&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
.header_bar {
    background-color: black;
    background: rgba(0,0,0,0.7);
    width: 100%;
    height: 80px;
    display: flex;
    align-items: center;
    position: fixed;
    z-index: 2;
}

.main_title {
    font-size: 55px;
    font-family: 'Teko', sans-serif;
    font-weight: 600;
    padding-left: 220px;
}

.main_title .title1 {
    color : #A29C9c
}
.main_title .title2 {
    color: #EEE6E6;
}

.menu_bar {
    display: flex;
    align-items: center;
    position: relative;
    left: 800px;
}

.menu_bar .menu {
    width: 120px;
    display: flex;
    color : white;
    font-size: 25px;
    font-weight: 400;
    font-family: 'Jua', sans-serif;
    justify-content: space-between;
    padding-right: 90px;
}

.search_bar {
    width: 300px;
    height: 35px;
    display: flex;
    align-items: center;
    background-color: white;
    border-radius: 8px;
    padding-left: 12px;
}

.search_bar input {
    background-color: white;
    width: 230px;
    border: none;
    font-size: 15px;
    font-weight: 400;
    font-family: 'Jua', sans-serif;
}

.search_bar input::-webkit-input-placeholder {
    font-size: 18px;
    font-weight: 400;
    font-family: 'Jua', sans-serif;
}

.search_bar input:-ms-input-placeholder {
    font-size: 18px;
    font-weight: 400;
    font-family: 'Jua', sans-serif;
}

.search_bar input::-ms-input-placeholder {
    font-size: 18px;
    font-weight: 400;
    font-family: 'Jua', sans-serif;
}

.search_bar input::placeholder {
    font-size: 18px;
    font-weight: 400;
    font-family: 'Jua', sans-serif;
}

.search_bar input:focus {outline:none;}

.search_bar button {
    width: 50px;
    border: none;
    background-color: white;
    cursor: pointer;
}
.summoner_proflie {
    width: 400px;
    background: rgba(0,0,0,0.7);
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
    font-weight: 400;
    font-family: 'Jua', sans-serif;
    border: whitesmoke;
    border-style: solid;
    border-width: 2px;
}

.summoner_proflie h1 {
    font-size: 27px;
}

.summoner_proflie h3 {
    font-size: 17px;
}

.summoner_proflie .proflie_icon {
    width: 100px;
    height: 100px;
}
.rank_info {
    background: rgba(0,0,0,0.7);
    color: white;
    width: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 17px;
    font-weight: 400;
    font-family: 'Jua', sans-serif;
    border: whitesmoke;
    border-style: solid;
    border-width: 2px;
}

.rank_info img {
    width: 100px;
    height: 100px;
}
.tooltip {
    position: relative;
}
.tooltip .tt-text {
    position: absolute;
    visibility: hidden;
    background-color: black;
    color: white;
    border-radius: 8px;
    z-index: 1;
    top: 100%;
    left: 50%;
    text-align: center;
}
.tooltip:hover .tt-text { 
    visibility: visible; 
}

.match_list {
    display: inline-block;
    padding-left: 120px;
}

.match_block {
    padding-bottom: 35px;
}
.match_win {
    background: rgba(163,207,236,0.8);
    border: whitesmoke;
    border-style: solid;
    border-width: 2px;
}
.match_lose {
    background: rgba(226,182,179,0.8);
    border: whitesmoke;
    border-style: solid;
    border-width: 2px;
}

.match_info {
    display: table;
    width: 1100px;
    height: 160px;
    color: #555E5E;
    font-weight: 400;
    font-family: 'Noto Sans KR', sans-serif;
}

.match_info .game_info {
    display: table-cell;
    vertical-align: middle;
}


.game_stats {
    width: 110px;
    height: 150px;
    font-size: 18px;
    text-align: center;
}
.game_type, .game_time,
.game_date {
    height: 22px;
}
.game_date .tt-text {
    white-space: nowrap;
    line-height: 40px;
    font-size: 14px;
    margin-left: -110px;
    padding-left: 13px;
    padding-right: 13px;
}
.line {
    width: 40px;
    height: 2px;
    background-color: rgb(112, 96, 96);
    margin: 5px auto;
}
.match_win .gameResult {
    color: rgb(11, 154, 236);
    height: 22px;
}
.match_lose .gameResult {
    color: rgb(194, 70, 61);
    height: 22px;
}

.champion_info {
    width: 200px;
    display: inline-block;
}
.info_imgs {
    display: flex;
    justify-content: center;
}
.champ_img img{
    width: 80px;
    height: 80px;
    border-radius: 50px;
}
.champ_img .tt-text {
    line-height: 30px;
    white-space: nowrap;
    padding-left: 10px;
    padding-right: 10px;
    font-size: 14px;
    margin-left: -35px;
}
.spell_img {
    display: inline;
    padding-left: 10px;
}
.spell_img img {
    width: 40px;
    height: 40px;
    border-radius: 10px;
}
.spell_img .tt-text {
    display: inline-block;
    width: 250px;
    text-align: left;
    font-size: 15px;
    padding: 12px;
}
.spell_img .spell_name {
    padding-bottom: 5px;
    color: rgb(214, 214, 36);
}
.spell_img .spell_description {
    line-height: 22px;
}
.rune_img {
    display: inline-block;
    padding-left: 10px;
}
.rune_img .tt-text {
    display: inline-block;
    width: 250px;
    text-align: left;
    font-size: 15px;
    padding: 12px;
}
.rune_img .rune_name {
    padding-bottom: 5px;
    color: rgb(214, 214, 36);
}
.rune_img .rune_description {
    line-height: 22px;
}
.sub_rune .tt-text {
    width: 28px;
    color: rgb(214, 214, 36);
    text-align: center;
    padding: 8px;
}
.main_rune, .sub_rune {
    width: 40px;
    height: 40px;
    border-radius: 50px;
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
}
.main_rune img {
    width: 40px;
    height: 40px;
}
.sub_rune img {
    width: 30px;
    height: 30px;
}
.champion_name {
    text-align: center;
    padding-top: 15px;
}

.game_score, .game_score2 {
    text-align: center;
    width: 120px;
}
.game_score .kda {
    font-size: 19px;
}
.game_score .kda_perfect {
    width: 80px;
    color: black;
    background-color: #C59024;
    margin: 2px auto;
    border-radius: 7px;
}
.game_score2 {
    font-size: 18px;
}

.game_itemList {
    width: 210px;
}
.items {
    margin:0 auto;
    width: 180px;
    display: flex;
    flex-wrap: wrap;
}
.game_item {
   width: 42px;
   height: 42px;
}
.items img {
    width: 38px;
    height: 38px;
    border-radius: 7px;
}
.items .tt-text {
    display: inline-block;
    width: 300px;
    text-align: left;
    font-size: 15px;
    padding: 12px;
    line-height: 20px;
}
.items .item_name {
    color: rgb(214, 214, 36);
    padding-bottom: 10px;
}
.items .tt-text a {
    color : whitesmoke;
}
.none_img {
    width: 38px;
    height: 38px;
    background-color: rgb(95, 132, 156);
    border-radius: 7px;
}
.wards {
    font-size: 15px;
    text-align: center;
}


.game_team {
    width : 300px;
}
.team {
    float: left;
    padding-right: 20px;
}
.player {
    display: flex;
    align-items: center;
}
.player_img {
    width: 28px;
    height: 28px;
}
.player_img img{
    width: 25px;
    height: 25px;
}
.player_name {
    width: 80px;
    max-width: 150px;
    padding-left: 5px;
    font-size: 15px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    vertical-align: middle;
    position: relative;
}
.player_img .tt-text {
    line-height: 30px;
    white-space: nowrap;
    padding-left: 10px;
    padding-right: 10px;
    font-size: 14px;
}

body {
    background-image: url("http://ddragon.leagueoflegends.com/cdn/img/champion/splash/Akali_9.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
}

section {
    padding-top: 60px;
    display: flex;
}

header {
    height: 80px;
}

.summoner_info {
    display: inline-block;
    padding-left: 90px;
}





